import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState } from '../index'
import { roladPage } from './actions'

export function useExchangeState(): AppState['Exchange'] {
  return useSelector<AppState, AppState['Exchange']>((state) => state.Exchange)
}

export function useExchangeHandlers(): {
  onIsRefresh:(isRefresh:boolean)=>void
} {
  const dispatch = useDispatch<AppDispatch>()
  const onIsRefresh = useCallback((isRefresh:boolean) => {
    dispatch(roladPage({isRefresh}))
  }, [dispatch])

  return { onIsRefresh }
}
